export default {
  isError(result) {
    if (result && result.error_code === 0)
      return false;
    return true;
  },
  isSuccess(result) {
    return !this.isError(result);
  },
  getErrorMessage(result) {
    if (this.isError(result)) {
      return result.error.msg == null ? '访问服务失败，请稍后重试' : result.error.msg;
    }
  },
  getSuccessData(result) {
    if (!this.isError(result)) {
      return { data: result.data };
    } else {
      return { data: [] };
    }
  }
}



