import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const test = r => require.ensure([], () => r(require('@/pages/test/index')), 'test')
const mrType = r => require.ensure([], () => r(require('@/pages/marketresearch/mrType')), 'mrType')
const purchaseMarket = r => require.ensure([], () => r(require('@/pages/marketresearch/purchase/market')), 'purchaseMarket')
const saleMarket = r => require.ensure([], () => r(require('@/pages/marketresearch/sale/market')), 'saleMarket')
const purchaseList = r => require.ensure([], () => r(require('@/pages/marketresearch/purchase/list')), 'purchaseList')
const saleList = r => require.ensure([], () => r(require('@/pages/marketresearch/sale/list')), 'saleList')
const purchaseDetail = r => require.ensure([], () => r(require('@/pages/marketresearch/purchase/detail')), 'purchaseDetail')
const purchaseRecord = r => require.ensure([], () => r(require('@/pages/marketresearch/purchase/record')), 'purchaseRecord')
const saleRecord = r => require.ensure([], () => r(require('@/pages/marketresearch/sale/record')), 'saleRecord')


const router = new Router({
  routes: [
    { path: '/test', name: 'test', component: test },
    { path: '/', component: mrType },
    { path: '/purchase/market', component: purchaseMarket },
    { path: '/purchase/market/task', component: purchaseList },
    { path: '/purchase/detail/:skuId', component: purchaseDetail},
    { path: '/purchase/market/record', component: purchaseRecord},
    { path: '/sale/market', component: saleMarket },
    { path: '/sale/market/task', component: saleList },
    { path: '/sale/market/record', component: saleRecord}
  ]
})
export default router



