

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import VueScroller from 'vue-scroller'
import store from './vuex'
import serviceUtil from '@/utils/result'
import LStorage from '@/utils/LStorage'
import router from './router'
import mixin from '@/mixins'
import '@/styles/home/index.less'

Vue.mixin(mixin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(VueScroller)

Vue.prototype.apiOk = result => {
return serviceUtil.isSuccess(result);
}
Vue.prototype.apiMessage = result => {
return serviceUtil.getErrorMessage(result);
}
Vue.prototype.apiData = result => {
return serviceUtil.getSuccessData(result);
}

Vue.prototype.toast = (message = '数据获取失败') => {
Vue.$vux.toast.text(message)
}

Vue.prototype.showLoading = (message = '加载中') => {
Vue.$vux.loading.show({
text: message
})
}
Vue.prototype.hideLoading = () => {
Vue.$vux.loading.hide()
}
Vue.prototype.$LStorage = LStorage

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
store,
router,
render: h => h(App)
}).$mount('#app-box')



